

import FastClick from 'fastclick'
import App from './App'
import Vue from 'vue'
import store from './store/store'
import router from './router'
import CommonFooter from './components/common/CommonFooter'
import api from './axios/index'
import './assert/icon/iconfont.css'
import vconsole from './utils/vconsole' 
import './utils/directive'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import AjaxPlugin from 'vux/src/plugins/ajax/index.js'

import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import VueCropper from 'vue-cropper'

FastClick.attach(document.body) 
Vue.use(api)
Vue.use(store)
Vue.use(ToastPlugin)
Vue.use(LoadingPlugin)
Vue.use(AjaxPlugin)
Vue.use(ElementUI)
Vue.use(VueCropper)


Vue.component('CommonFooter', CommonFooter)
Vue.component('x-header', XHeader)
Vue.component('v-transfer-dom', Actionsheet)



Vue.config.productionTip = true



import VueWechatTitle from 'vue-wechat-title'
Vue.use(VueWechatTitle)

router.beforeEach((to, from, next) => {
if (to.meta.title) {
document.title = to.meta.title
}
next()
})


new Vue({
router,
store,
render: h => h(App)
}).$mount('#app-box')



